*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: montsererat, sans-serif;
}

input, button {
    appearance: none;
    background: none;
    border: none;
    outline: none;
}

.App{
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #53565A;
}

form{
    display: block;
    position: relative;
}

form:after {
    content: '';
    display: block;
    position: absolute;
    top: -5px;
    left: -5px;
    right: -5px;
    bottom: -5;
    z-index: 1;
    background-image: linear-gradient(to bottom right, #5C93CC, #194390);
}

form .form-inner {
    position: relative;
    display: block;
    background-color: #fff;
    padding: 30px;
    z-index: 2;
}

form .form-inner h2 {
   color: #888;
   font-size: 28px;
   font-weight: 500;
   margin-bottom: 30px;
}

form .form-inner .form-group {
    display: block;
    width: 300px;
    margin-bottom: 15px;
}

.form-inner .form-group label {
    display: block;
    color: #666;
    font-size: 12px;
    margin-bottom: 5px;
    transition: 0.4s;
}

.form-inner .form-group:focus-within label {
    color: #FE4880;
}

form .form-inner .form-group input {
    display: block;
    width: 100%;
    padding: 10px 15px;
    background-color: #F8F8F8;
    border-radius: 8px;
    transition: 0.4s;
}

form .form-inner input:focus {
    box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
}

form .form-inner input[type="submit"], .admin-dashboard button {
    display: inline-block;
    padding: 10px 15px;
    border-radius: 8px;
    background-image: linear-gradient(to bottom right, #5C93CC, #194390);
    background-size: 200%;
    background-position: 0%;
    transition: .4s;
    color: #fff;
    font-weight: 700;
    cursor: pointer;
}

form .form-inner input[type='submit']:hover, .admin-dashboard button:hover {
    background-position: 100% 0%;
}

.error {
    color: palevioletred;
    margin-bottom: 30px;
}

.admin-dashboard{
    width: 100%;
    height: 100%;
    background-color: #fff;
    padding: 50px 0;
    text-align: center;
}

.admin-dashboard h2{
    color: #888;
    font-size: 28px;
    font-weight: 500;
    margin-top: 00px;
    margin-bottom: 30px;
    text-align: center;
}

.admin-dashboard h2 span {
    color: red;
    font-weight: 700;
}

.logoutButton {
    text-align: center;
    margin-bottom: 30px;
}

/* map */
.carMap{
    width: 80%;
    height: 70vh;
    text-align: center;
    justify-content: center;
    margin: 0 auto;
}
